import React, { useContext, useEffect } from "react";
import classes from "./Pages.module.scss";
import { Context } from "../../index";
import ProductCard from "../Products/ProductCard/ProductCard";
import Loader from "../Loader/Loader";
import { useParams } from "react-router-dom";
import { LINK_CATEGORY } from "../../utils/consts";

const Category = () => {
  const { fetching, products, loading } = useContext(Context);
  const { page } = useParams();

  // eslint-disable-next-line
  useEffect(() => fetching(LINK_CATEGORY + page), [page]);

  if (loading) {
    return <Loader />;
  }

  return (
    <div>
      <h2 className={classes.title}>{page}</h2>
      {products.map((productItem) => {
        return <ProductCard key={productItem.id} item={productItem} />;
      })}
    </div>
  );
};

export default Category;
