import React from "react";
import classes from "./ProductSlider.module.scss";
import "react-multi-carousel/lib/styles.css";
import Carousel from "react-multi-carousel";
import ProductCard from "../ProductCard/ProductCard";
import Icons from "../../Icons";

const ProductSlider = ({ productList }) => {
  const responsive = {
    // desktop: {
    //   breakpoint: { max: 3000, min: 1024 },
    //   items: 3,
    //   slidesToSlide: 3, // optional, default to 1.
    // },
    // tablet: {
    //   breakpoint: { max: 1024, min: 464 },
    //   items: 2,
    //   slidesToSlide: 2, // optional, default to 1.
    // },
    mobile: {
      breakpoint: { max: 3000, min: 0 },
      items: 1,
      slidesToSlide: 1, // optional, default to 1.
    },
  };

  const CustomArrow = ({ onClick, button }) => {
    return (
      <div className={classes[button]} onClick={onClick}>
        <Icons icon={"arrow"} />
      </div>
    );
  };

  return (
    <Carousel
      swipeable={true}
      draggable={true}
      responsive={responsive}
      infinite={true}
      transitionDuration={500}
      containerClass="carousel-container"
      customLeftArrow={<CustomArrow button={"prev"} />}
      customRightArrow={<CustomArrow button={"next"} />}
    >
      {productList.map((productItem) => (
        <ProductCard key={productItem.id} item={productItem} />
      ))}
    </Carousel>
  );
};

export default ProductSlider;
