import React, { useContext, useEffect } from "react";
import PromoBlock from "../PromoBlock/PromoBlock";
import ProductSectionHeader from "../Products/ProductSectionHeader/ProductSectionHeader";
import ProductSlider from "../Products/ProductSlider/ProductSlider";
import { Context } from "../../index";
import { LINK_DISCOUNTS, LINK_POPULAR } from "../../utils/consts";
import Loader from "../Loader/Loader";

const Home = () => {
  const { fetching, products, loading } = useContext(Context);
  const discountedProducts = products.filter((item) => item.discounts);
  const popularProducts = products.filter((item) => item.popular);

  // eslint-disable-next-line
  useEffect(() => fetching("/products"), []);

  if (loading) {
    return <Loader />;
  }

  return (
    <div>
      <PromoBlock />
      <ProductSectionHeader title={"Discounts"} link={LINK_DISCOUNTS} />
      <ProductSlider productList={discountedProducts} />
      <ProductSectionHeader title={"Most popular"} link={LINK_POPULAR} />
      <ProductSlider productList={popularProducts} />
    </div>
  );
};

export default Home;
